#home{
    h1{
        color: $secondary;
        text-align: center;
        font-weight: 600;
        span{
            display: inline-block;
        }
    }
    #tile-hero{
        background: url('../img/orchid-background.png');
        background-repeat: no-repeat;
        background-size: 110em;
        background-position-y: -5em;
        .product-image{
            padding-left: 38%;
            display: inline-block;
            float: left;
            width: 39em;
        }
    }
    #tile-lifestyle{
        background: url('../img/yoga-girls.png') center;
        background-position: center;
        background-repeat: no-repeat;
        padding: 25em 0em 3em 0em;
        h1{
            color: white;
            background: $primary;
            padding: .25em;
        }
    }
    #tile-cta{
        text-align: center;
        span{
            display: block;
            color: $secondary;
            font-size: .8em;
            font-weight: 700;
        }
    }
    .bullet-points{
        float: left;
        display: inline-block;
        margin-top: 7em;
        .bullet-icon{
            background: white;
            color: $secondary;
            border-radius: 25px;
            border: 1px solid darken(white, 20%);
            display: inline-block;
            width: 47px;
            height: 47px;
            padding: .5em;
            margin-right: .5em;
            -webkit-box-shadow: -3px 5px 5px 0px rgba(0,0,0,0.17);
            -moz-box-shadow: -3px 5px 5px 0px rgba(0,0,0,0.17);
            box-shadow: -3px 5px 5px 0px rgba(0,0,0,0.17);
        }
        li{
            margin-bottom: 1em;
            font-weight: 600;
            color: $secondary;
            font-size: 1.2em;
            &:last-child{
                margin-bottom: 0;
            }
        }
    }
    #tile-clinical-keypoints, #tile-main-keypoints{
        background: rgba(255, 255, 255, .7);
    }
    #tile-main-keypoints{
        [class^="span"]{
            &:first-child{
                padding-right: 2em;
                border-right: 1px dashed lighten(black, 80%);
            }
            &:last-child{
                padding-left: 2em;
            }
        }
        .main-keypoint{
            h3{
                color: $accent;
                font-size: 1.25em;
                margin-bottom: .15em;
            }
            margin-bottom: 1.5em;
        }
    }
    #tile-clinical-keypoints{
        .clinical-keypoint{
            text-align: center;
            img.icon-tulip{
                width: 2em;
                margin: 0 auto;
                text-align: center;
            }
            h4{
                text-align: center;
                font-size: 1.2em;
                font-weight: 700;
                margin-bottom: .5em;
                margin-top: .5em;
                color: $secondary;
            }
            padding: 0 2em;
            border-left: 1px dashed lighten(black, 80%);
            border-right: 1px dashed lighten(black, 80%);
            &:first-child{
                padding-left: 0;
                border-right: 0;
                border-left: 0;
            }
            &:last-child{
                padding-right: 0;
                border-right: 0;
            }
        }
    }
    @media screen and (max-width: 1200px) {
        #tile-hero{
            background-size: 179%;
            background-position-x: -4em;
            .product-image{
                width: 13em;
                padding-left: 0;
                margin-left: 18%;
            }
            h1{
                font-size: 1.75em;
            }
        }
        .bullet-points{
            margin-top: 5em;
        }
    }
    @media screen and (max-width: 960px) {
        #tile-clinical-keypoints{
            .clinical-keypoint{
                border: 0;
                padding: 0;
                padding-bottom: 1em;
                border-bottom: 1px dashed lighten(black, 80%);
                width: 100%;
                margin-bottom: 1em;
                &:last-child{
                    margin-bottom: 0;
                    border: 0;
                    padding-bottom: 0;
                }
            }
        }
        #tile-cta{
            border-top: 1px solid darken(white, 15%);
            border-bottom: 1px solid darken(white, 15%);
        }
        #tile-lifestyle{
            h1{
                padding: .5em;
                font-size: 1.5em;
                line-height: 1.25em;
                span{
                    display: inline-block;
                }
            }
        }
        .background-area{
            background: none;
        }
    }



    // Responsive
    @media screen and (max-width: 900px) {
        #tile-hero{
            background-width: 200%;
            .product-image{
                margin-left: 10%;
            }
        }
    }
    @media screen and (max-width: 800px) {
        #tile-hero{
            background: url('../img/wood-background.png');
            text-align: center;
            .bullet-points{
                margin-top: 0;
                margin-left: 0;
                text-align: center;
                width: 100%;
                margin-bottom: 2em;
            }
            .product-image{
                display: inline-block;
                text-align: center;
                width: 15em;
                margin-left: 25%;
            }
        }
    }
    @media screen and (max-width: 625px) {
        #tile-hero{

        }
    }
    @media screen and (max-width: 625px) {
        #tile-main-keypoints{
            [class^=span]{
                width: 100%;
                &:first-child{
                    padding-right: 0;
                    border: 0;
                }
                &:last-child{
                    padding-left: 0;
                }
            }
            .main-keypoint{
                &:last-child{
                }
            }
        }
    }
    @media screen and (max-width: 500px) {
        #tile-hero{
            .bullet-points{
                font-size: .9em;
            }
            .product-image{
                margin-left: 15%;
            }
        }
    }
}
